@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

footer{
    border-top: 0.5px solid grey;
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 8vh;
    background-color: #fff;
   
    position:absolute;
    bottom: 0px;
    width: 100%;
    position :fixed;
    
}


.nav_links{
    display:flex;
    color: rgb(12, 11, 11);
    text-decoration: none;
    letter-spacing: 3px;
    font-weight: bold;
    font-size: 14px;
    justify-content: space-around;
    width: 30%;
}
