

.container_example{
  
    width: 100%;
    display: grid;
    grid-template-columns: 15% auto;
    grid-template-rows: 611px;
    grid-template-areas: 
    "nav content"
    ;
    
}
.nav_item{
     grid-area: nav;
}
.content_example{   
    grid-area: content;
    background-color: white;
   display: grid;
   grid-template-columns: 1fr;
   
 
}
.example_item_list{
    width: 100%;
   
    background-color: #fff;
  
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  justify-items: center;

}
.example_item_main{
    border-radius: 10px;
    margin: 20px;
    width: 200px;
    height: 200px;
    background-color: #fff;
    border: 1px solid rgb(234, 228, 228);
    
}

@media only screen and (max-width: 768px){
    .nav_item{
      display: none;
    }
    .example_item_list{  
        background-color: #fff;
        margin: 0 auto;
      
       display: grid;
       grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr ;
      justify-items: center;
      column-gap: 15px;
    }
    .container_example{
  
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 611px;
        grid-template-areas: 
        "content";
        justify-items: center;
    }
}

@media only screen and (max-width: 400px){
    .nav_item{
      display: none;
    }
    .example_item_list{  
        background-color: #fff;
        margin: 0 auto;
       
       display: grid;
       grid-template-columns: 1fr;
      grid-template-rows: 1fr auto;
      justify-items: center;
      column-gap: 15px;
    }
    .container_example{
  
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 611px;
        grid-template-areas: 
        "content";
        justify-items: center;
    }
}