*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
} 

.app-container{
  width: 100%;
  height: 100vh;

    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 40px 362px 38.55px;
    grid-template-areas: 
    "header "
    "routes "
    "footer ";
}

.header{
    grid-area: header;
}
.routes{
    grid-area: routes;

}
.footer{
    grid-area: footer;

}